<div class="theater-container"><div class="theater-piece" v-for="(asset, idx) in assets" :key="idx"><img class="image" :src="asset.img"><img class="audio-button" v-if="currentlyPlayingAudioKey === asset.key" src="../../public/images/pause-button.svg" @click="triggerAudio(asset.key)"><img class="audio-button" v-else src="../../public/images/play-button.svg" @click="triggerAudio(asset.key)"></div></div>